@font-face {
  font-family: "Inter";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/inter-regular.woff2") format("woff2");
}
@font-face {
  font-family: "Inter";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/inter-medium.woff2") format("woff2");
}
@font-face {
  font-family: "Inter";
  font-weight: 600;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/inter-semibold.woff2") format("woff2");
}
@font-face {
  font-family: "Inter";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/inter-bold.woff2") format("woff2");
}
html {
  height: 100%;
}

.page {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  margin: 0;
  min-width: 1440px;
  font-family: "Inter", "Arial", sans-serif;
  font-size: 18px;
  line-height: 28px;
  color: #2d3440;
}
.page--pink {
  background-color: #feafc3;
}
.page--blue {
  background-color: #69a9ff;
}
.page--yellow {
  background-color: #fcc850;
}

img {
  max-width: 100%;
  height: auto;
}

.main-container {
  flex-grow: 1;
}

.page-container {
  width: 1170px;
  margin: 0 auto;
  padding: 0 20px;
  margin-top: 80px;
  margin-top: 30px;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.navigation {
  width: 1170px;
  margin: 0 auto;
  padding: 0 20px;
  margin-top: 80px;
  display: flex;
  align-items: center;
  padding-top: 25px;
  padding-bottom: 25px;
  margin-top: 0;
}

.navigation__logo {
  flex-shrink: 0;
  margin-right: 30px;
  transition: 0.3s;
}
.navigation__logo:hover {
  opacity: 0.6;
}

.navigation__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.navigation__tel {
  text-decoration: none;
  color: #2d3440;
  margin-left: auto;
  margin-right: 30px;
  font-weight: 600;
  transition: 0.3s;
}
.navigation__tel:hover {
  opacity: 0.6;
}

.site-navigation__link {
  text-decoration: none;
  color: #2d3440;
  padding: 10px 15px;
  border: 2px solid transparent;
  border-radius: 26px;
  transition: 0.3s;
}
.site-navigation__link:hover {
  background-color: rgba(252, 252, 252, 0.5);
}
.site-navigation__link:focus {
  background-color: rgba(252, 252, 252, 0.5);
  border-color: #2d3440;
}
.site-navigation__link:active {
  background-color: #fcfcfc;
  border-color: transparent;
}
.site-navigation__link--current {
  background-color: #ff4a78;
  color: #fcfcfc;
}

.user-navigation {
  gap: 20px;
}

.user-navigation__item {
  position: relative;
}

.user-navigation__link {
  text-decoration: none;
  color: #2d3440;
  position: relative;
  padding-left: 45px;
  padding-right: 25px;
  padding-top: 10px;
  padding-bottom: 10px;
  background-color: rgba(252, 252, 252, 0.3);
  border: 2px solid transparent;
  border-radius: 26px;
  transition: 0.3s;
}
.user-navigation__link::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 15px;
  width: 15px;
  height: 15px;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  transform: translateY(-50%);
}
.user-navigation__link--search {
  padding-right: 0;
}
.user-navigation__link--search::before {
  background-image: url("../img/icon-search.svg");
}
.user-navigation__link--login::before {
  background-image: url("../img/icon-login.svg");
}
.user-navigation__link--cart::before {
  background-image: url("../img/icon-cart.svg");
}
.user-navigation__link--full-cart::before {
  background-image: url("../img/icon-cart-full.svg");
}
.user-navigation__link--selected {
  background-color: #fcfcfc;
}
.user-navigation__link:hover {
  background-color: rgba(252, 252, 252, 0.5);
}
.user-navigation__link:focus {
  border-color: #2d3440;
}
.user-navigation__link:active {
  background-color: #fcfcfc;
  border-color: transparent;
}

.popover {
  position: absolute;
  top: 50px;
  right: 0;
  display: none;
  box-sizing: border-box;
  background-color: #fcfcfc;
  border-radius: 4px;
}
.popover--search {
  width: 356px;
  padding: 20px;
}
.popover--login {
  width: 420px;
  padding: 50px;
}
.popover--cart-full {
  width: 441px;
  padding: 50px;
}
.popover--cart-empty {
  width: 288px;
  padding: 50px;
}

.popover__title {
  margin: 0;
  font-size: 24px;
  line-height: 30px;
  font-weight: 700;
}
.popover__title--search, .popover__title--cart {
  margin-bottom: 30px;
}
.popover__title--cart-empty {
  text-align: center;
}

.search-form {
  display: flex;
}

.search-form__field {
  padding: 15px;
  width: 100%;
  color: #b9b9b9;
}

.search-form__button {
  display: none;
}

.login-form__group {
  margin-bottom: 15px;
}

.login-form__field {
  width: 100%;
}

.login-form__wrapper {
  display: flex;
  align-items: center;
  margin-top: 30px;
}

.login-form__button {
  margin-right: 25px;
}

.login-form__link {
  color: inherit;
  font-size: 14px;
  line-height: 20px;
}

.form-field {
  padding: 15px;
  box-sizing: border-box;
  font-size: 16px;
  line-height: 20px;
  color: #2d3440;
  border: 1px solid #b9b9b9;
  border-radius: 4px;
  transition: 0.3s;
}
.form-field::placeholder {
  color: #b9b9b9;
}
.form-field:hover {
  border-color: #2d3440;
}
.form-field:hover::placeholder {
  color: #2d3440;
}
.form-field:disabled {
  background-color: #b9b9b9;
}
.form-field:disabled::placeholder {
  color: #2d3440;
}
.form-field--error {
  border: 1px solid #ea5454;
}
.form-field--error::placeholder {
  color: #ea5454;
}
.form-field--error:hover {
  border: 1px solid #ea5454;
}

.button {
  padding: 12px 32px;
  font-size: 16px;
  line-height: 20px;
  font-weight: 700;
  text-decoration: none;
  color: #2d3440;
  background-color: #ffffff;
  border: 4px solid rgba(254, 175, 195, 0.6);
  border-radius: 26px;
  box-shadow: 0 4px 12px rgba(45, 52, 64, 0.1);
  transition: 0.3s;
}
.button:hover:not(:disabled) {
  background-color: rgba(255, 255, 255, 0.4);
  border: 4px solid #ffffff;
  box-shadow: 0 4px 15px rgba(133, 133, 133, 0.15);
}
.button:disabled {
  font-weight: 400;
  color: #b9b9b9;
  background-color: #e7e7e7;
  border: 4px solid rgba(255, 255, 255, 0.4);
}

.button--special {
  color: #ffffff;
  background-color: #ff2f64;
  border: 4px solid rgba(255, 255, 255, 0.6);
}
.button--special:hover:not(:disabled) {
  color: #2d3440;
  background-color: rgba(252, 252, 252, 0.4);
  border: 4px solid #ff2f64;
}
.button--special:disabled {
  color: #565c66;
  background-color: #b9b9b9;
  border: 4px solid rgba(185, 185, 185, 0.3);
}

.button-small {
  padding: 8px 50px;
  font-weight: 700;
  background-color: rgba(252, 252, 252, 0.3);
  border: 2px solid #fcfcfc;
  border-radius: 20px;
  transition: 0.3s;
}
.button-small:hover:not(:disabled) {
  background-color: #fcfcfc;
  border-color: rgba(254, 175, 195, 0.6);
}
.button-small:disabled {
  opacity: 0.4;
}

.button-close {
  width: 20px;
  height: 20px;
  border: none;
  background-color: transparent;
  background-image: url("../img/icon-cross.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.button-close:hover {
  opacity: 0.6;
}

.page-footer {
  width: 1170px;
  margin: 0 auto;
  padding: 0 20px;
  margin-top: 80px;
  display: flex;
  justify-content: space-between;
  padding: 50px 0;
  margin-top: 0;
}

.social__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.social__item:not(:last-of-type) {
  margin-right: 15px;
}

.social__link {
  display: block;
  width: 28px;
  height: 28px;
  border-radius: 50%;
  transition: 0.3s;
}
.social__link:hover {
  background-color: #2d3440;
}
.social__link--vk {
  background-image: url("../img/icon-vk.svg");
  background-repeat: no-repeat;
}
.social__link--fb {
  background-image: url("../img/icon-fb.svg");
  background-repeat: no-repeat;
}
.social__link--inst {
  background-image: url("../img/icon-inst.svg");
  background-repeat: no-repeat;
}

.footer-navigation {
  display: flex;
  font-size: 14px;
  line-height: 20px;
}

.footer-navigation__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  margin-left: 25px;
}

.footer-navigation__item {
  margin-bottom: 8px;
}

.footer-navigation__link {
  text-decoration: none;
  color: #2d3440;
  position: relative;
  transition: 0.3s;
}
.footer-navigation__link:hover {
  opacity: 0.6;
}
.footer-navigation__link--special {
  font-weight: 700;
}
.footer-navigation__link--special::before {
  content: "";
  position: absolute;
  top: 1px;
  left: -25px;
  width: 14px;
  height: 14px;
  background-image: url("../img/icon-heart.svg");
  background-repeat: no-repeat;
}

.footer-copyright__logo {
  display: block;
  margin-bottom: 15px;
  width: 93px;
  height: 27px;
  background-image: url("../img/logo-htmlacademy.svg");
  transition: 0.3s;
}
.footer-copyright__logo:hover {
  opacity: 0.6;
}

.footer-copyright__text {
  margin: 0;
  font-size: 14px;
  line-height: 20px;
}

.gifts {
  width: 1170px;
  margin: 0 auto;
  padding: 0 20px;
  margin-top: 80px;
}

.gifts__title {
  margin: 0;
  font-size: 32px;
  line-height: 46px;
  text-align: center;
  margin-bottom: 55px;
}

.gifts__wrapper {
  display: flex;
  justify-content: space-between;
}

.gifts__card {
  box-sizing: border-box;
  padding: 40px;
  padding-right: 210px;
  width: 570px;
  background-color: #ff7799;
  border-radius: 16px;
}
.gifts__card--berry {
  background-image: url("../img/gift-berry.png");
  background-repeat: no-repeat;
  background-position: bottom right 15px;
}
.gifts__card--marshmellow {
  background-image: url("../img/gift-marshmellow.png");
  background-repeat: no-repeat;
  background-position: bottom right;
}

.gifts__card-title {
  margin: 0;
  margin-bottom: 15px;
  font-size: 24px;
  line-height: 30px;
}

.gifts__description {
  margin: 0;
  margin-bottom: 30px;
  font-size: 16px;
  line-height: 22px;
}

.gifts__button {
  display: inline-block;
}

.popular {
  width: 1170px;
  margin: 0 auto;
  padding: 0 20px;
  margin-top: 80px;
}

.popular__title {
  margin: 0;
  font-size: 32px;
  line-height: 46px;
  text-align: center;
  margin-bottom: 110px;
}

.popular__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 30px;
}

.card {
  position: relative;
  box-sizing: border-box;
  padding: 40px;
  width: 270px;
  min-height: 280px;
  background-color: #ffffff;
  border-radius: 8px;
}

.card__image {
  position: absolute;
  top: -55px;
  left: 50%;
  transform: translateX(-50%);
}

.card__title {
  margin: 0;
  margin-top: 95px;
  margin-bottom: 10px;
  font-size: 20px;
  line-height: 24px;
  font-weight: 700;
  text-align: center;
}

.card__description {
  margin: 0;
  margin-bottom: 20px;
  font-size: 16px;
  line-height: 22px;
  text-align: center;
}

.card__buy {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.card__price {
  font-size: 20px;
  line-height: 24px;
  font-weight: 700;
}

.card__button {
  display: inline-block;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  border: 2px solid transparent;
  background-color: #ff2f64;
  background-image: url("../img/icon-cart-light.svg");
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
  transition: 0.3s;
}
.card__button:hover {
  background-color: #ffffff;
  background-image: url("../img/icon-cart-dark.svg");
  border-color: #ff2f64;
}
.card__button--disabled {
  background-color: #b9b9b9;
  background-image: url("../img/icon-cart-dark.svg");
}

.about {
  width: 1170px;
  margin: 0 auto;
  padding: 0 20px;
  margin-top: 80px;
}

.about__waffle {
  padding: 25px;
  border-radius: 16px;
  background-image: url("../img/back-waffle.jpg");
  background-color: #fcc850;
}

.about__wrapper {
  padding: 55px 40px;
  background-color: #ffffff;
  border-radius: 16px;
}

.about__title {
  margin: 0;
  font-size: 32px;
  line-height: 46px;
  text-align: center;
  margin-bottom: 55px;
}

.about__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  row-gap: 40px;
}

.about__item {
  position: relative;
  padding-left: 65px;
