.filter-control {
  position: relative;
  padding-left: 25px;
}

.filter-control__mark {
  position: absolute;
  left: 0;
  top: 50%;
  width: 16px;
  height: 16px;
  box-sizing: border-box;
  border: 2px solid #2d3440;
  border-radius: 2px;
  transform: translateY(-50%);
}
.filter-control__mark--radio {
  border-radius: 50%;
}

.filter-control__input[type=radio]:checked + .filter-control__mark::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background-color: #2d3440;
  transform: translate(-50%, -50%);
}

.filter-control__input[type=checkbox]:checked + .filter-control__mark {
  border: none;
}
.filter-control__input[type=checkbox]:checked + .filter-control__mark::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 16px;
  height: 16px;
  background-image: url("../img/icon-check-on.svg");
}

