  justify-content: center;
  align-items: center;
  width: 196px;
  height: 36px;
  border-radius: 20px;
  background-color: rgba(252, 252, 252, 0.3);
}

.range__scale {
  position: relative;
  width: 164px;
  height: 2px;
  background-color: rgba(45, 52, 64, 0.3);
}

.range__bar {
  position: absolute;
  height: 2px;
  background-color: #2d3440;
}

.range__toggle {
  position: absolute;
  top: -6px;
  width: 16px;
  height: 16px;
  border: none;
  border-radius: 50%;
  background-color: #2d3440;
}
.range__toggle::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 4px;
  height: 4px;
  border-radius: 50%;
  background-color: #fcfcfc;
  transform: translate(-50%, -50%);
}
.range__toggle--max {
  right: 0;
}

.pagination {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  align-items: center;
}

.pagination__item:not(:last-of-type) {
  margin-right: 20px;
}

.pagination__link {
  text-decoration: none;
  color: #2d3440;
  position: relative;
}
.pagination__link::before {
  content: "";
  position: absolute;
  z-index: -1;
  top: 50%;
  left: 50%;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  transform: translate(-50%, -50%);
  transition: 0.3s;
}
.pagination__link:hover::before {
  border: 1px solid #fcfcfc;
  background-color: rgba(252, 252, 252, 0.3);
}
.pagination__link--current::before {
  background-color: #fcfcfc;
}
.pagination__link--disabled {
  opacity: 0.2;
}
.pagination__link--prev {
  display: block;
  width: 16px;
  height: 16px;
  background-image: url("../img/arrow-left-dark.svg");
}
.pagination__link--next {
  display: block;
  width: 16px;
  height: 16px;
  background-image: url("../img/arrow-right-dark.svg");
}